<template>
  <div id="app">
    <transition :name="transitionName">
      <vue-page-stack>
        <router-view :key="$route.fullPath" class="router-view-c"></router-view>
      </vue-page-stack>
    </transition>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      transitionName: "forward",
    };
  },
  watch: {
    $route: {
      handler(to, from) {
        // if (this.$route.name === "home" || this.$route.name === "login") {
        //   document.getElementById("html").style.filter = "grayscale(1)";
        // } else {
        //   document.getElementById("html").style.filter = "";
        // }
        //主要用于移动端的前进/后退时的切换效果，模仿原生的切换，左滑/右滑
        if (to.params["stack-key-dir"] === "forward") {
          this.transitionName = "forward";
          this.$nextTick(() => {
            window.scrollTo(0, 0);
          });
        } else {
          this.transitionName = "back";
        }
      },
      immediate: true,
    },
  },
};
</script>

<style lang="less">
@import "./styles/reset.less";

:root {
  --primary-color: #6a347e;
}
#app {
  min-height: 100vh;

  .back-enter-active,
  .forward-enter-active {
    will-change: transform;
    transition: transform 250ms;
    width: 100%;
  }
  .forward-leave-to,
  .back-enter {
    transform: translateX(-100%);
  }
  .back-leave-to,
  .forward-enter {
    transform: translateX(100%);
  }
}
</style>
