import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "index",
    component: () =>
      import(/* webpackChunkName: "main" */ "../views/index.vue"),
    meta: {
      name: "今天吃啥",
    },
  },
  {
    path: "/settings",
    name: "settings",
    component: () =>
      import(/* webpackChunkName: "main" */ "../views/settings.vue"),
    meta: {
      name: "菜单设置",
    },
  },
];

const router = new VueRouter({
  mode: "history",
  base: "/food/",
  routes,
});

export default router;
